body {
  background: #171618;
  place-items: center;
  height: 100vh;
  margin: 0;
  display: grid;
}

a {
  color: #999;
  text-transform: uppercase;
  letter-spacing: 4px;
  cursor: pointer;
  padding: 30px 60px;
  font: 700 30px consolas;
  text-decoration: none;
  position: relative;
  overflow: hidden;
  box-shadow: 0 0 10px 5px #0006;
}

a span:first-child {
  background: linear-gradient(to right, #171618, #3bff3b);
  width: 100%;
  height: 3px;
  animation: 2s linear infinite animate1;
  position: absolute;
  top: 0;
  right: 0;
}

@keyframes animate1 {
  0% {
    transform: translateX(-100%);
  }

  100% {
    transform: translateX(100%);
  }
}

a span:nth-child(2) {
  background: linear-gradient(#171618, #3bff3b);
  width: 3px;
  height: 100%;
  animation: 2s linear 1s infinite animate2;
  position: absolute;
  top: 0;
  right: 0;
}

@keyframes animate2 {
  0% {
    transform: translateY(-100%);
  }

  100% {
    transform: translateY(100%);
  }
}

a span:nth-child(3) {
  background: linear-gradient(to left, #171618, #3bff3b);
  width: 100%;
  height: 3px;
  animation: 2s linear infinite animate3;
  position: absolute;
  bottom: 0;
  right: 0;
}

@keyframes animate3 {
  0% {
    transform: translateX(100%);
  }

  100% {
    transform: translateX(-100%);
  }
}

a span:nth-child(4) {
  background: linear-gradient(to top, #171618, #3bff3b);
  width: 3px;
  height: 100%;
  animation: 2s linear 1s infinite animate4;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes animate4 {
  0% {
    transform: translateY(100%);
  }

  100% {
    transform: translateY(-100%);
  }
}

.btn {
  justify-content: center;
  align-items: center;
  display: flex;
}

.next-button {
  color: #171618;
  font-size: 16px;
  font-family: 600;
  cursor: pointer;
  background-color: #ececec;
  border: none;
  outline: none;
  width: 150px;
  height: 30px;
  padding: 10px;
}
